@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap";
body {
  margin: 0;
  padding: 0;
}

* {
  font-family: Montserrat, sans-serif;
}

.main-link {
  color: #424141;
  font-size: 24px;
}

.main-nav {
  background: #a1a1a1;
  justify-content: space-between;
  gap: 20px;
  padding: 10px 20px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.page-error, .main {
  height: 100vh;
  width: 100%;
  background: #e2e2e2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-error__title {
  color: #000;
  text-align: center;
  margin: 0;
  font-size: 56px;
  font-weight: 400;
  line-height: 68px;
}

.page-error__title span {
  color: #000;
  font-size: 100px;
  font-weight: 400;
  line-height: 121px;
}

.modal-dialog {
  background: #fdfdfd;
  border-radius: 9px;
  padding: 44px 62px;
  box-shadow: 10px 10px 10px #0000000d;
}

.modal-dialog__wrapper {
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
}

.modal-dialog__title {
  color: #41403e;
  text-align: center;
  margin-bottom: 43px;
  font-family: Montserrat, sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
}

.modal-dialog__input {
  width: 100%;
  min-width: 370px;
  color: #41403e;
  border: none;
  border-bottom: 2px solid #6ca7ff;
  outline: none;
  margin-bottom: 29px;
  padding-bottom: 6px;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  display: block;
}

.modal-dialog__submit {
  min-width: 370px;
  min-height: 56px;
  color: #fff;
  cursor: pointer;
  background: #6ca7ff;
  border: none;
  border-radius: 9px;
  margin-top: 42px;
  margin-bottom: 43px;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  transition: all .3s ease-in-out;
}

.modal-dialog__submit:hover {
  background: #6196e6;
}

.modal-dialog__link {
  color: #6ca7ff;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  text-decoration-line: underline;
}

.button-back {
  color: #000;
  text-transform: uppercase;
  border: none;
  padding: 10px;
  font-size: 14px;
  position: absolute;
  top: 50px;
  right: 50px;
}

.button-back:hover {
  text-decoration: none;
}

.modal-dialog__edit-img {
  width: 100px;
  height: 100px;
  cursor: pointer;
  background: #8a8a8a63;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  padding: 10px;
  display: flex;
}

.modal-dialog__edit-img:hover {
  background: #8a8a8a80;
}

.modal-dialog__edit-img i {
  width: 80px;
  height: 80px;
  background: url("user.5bbfb0d2.svg") center / cover no-repeat;
  display: block;
}



.h1-404 {
  color: green;
  font-size: 100px;
}







.chat {
  display: flex;
}

.chat__leftbar {
  width: 396px;
  height: 100vh;
  background: #fbfbfb;
}

.chat__leftbar-header {
  background: #fbfbfb;
  border-bottom: 2px solid #d6d6d6;
  padding: 28px;
}

.chat__leftbar-header-top {
  justify-content: space-between;
  margin-bottom: 14px;
  display: flex;
}

.chat__leftbar-avatar {
  width: 40px;
  height: 40px;
  background: #d9d9d9;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
}

.chat__leftbar-avatar:hover {
  background: #b8b7b7;
}

.chat__leftbar-avatar i {
  width: 23px;
  height: 23px;
  background: url("user.5bbfb0d2.svg") center / cover no-repeat;
  display: block;
}

.chat__leftbar-header-btns {
  gap: 11px;
  display: flex;
}

.chat__leftbar-header-btn {
  width: 40px;
  height: 40px;
  border-radius: 9px;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
}

.chat__leftbar-header-btn:hover {
  background: #5a5a5a2c;
}

.chat__leftbar-header-btn i {
  width: 23px;
  height: 23px;
  object-fit: contain;
  display: block;
}

.chat__leftbar-header-btn--group i {
  background: url("group.850bc153.svg");
}

.chat__leftbar-header-btn--del i {
  background: url("delete.1cd645ea.svg");
}

.chat__leftbar-header-btn--insert i {
  background: url("insert_image.9245b797.svg");
}

.chat__leftbar-header-btn--menu i {
  background: url("menu_bars.2f1cce18.svg");
}

.chat__leftbar-header-search {
  height: 27px;
  width: 100%;
  background: #d9d9d9;
  border: none;
  border-radius: 9px;
  outline: none;
  padding: 0 18px 0 26px;
}

.chat__leftbar-header-search::placeholder {
  color: #41403e;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
}

.chat__leftbar-mesitem {
  cursor: pointer;
  background: #fff;
  border-bottom: 1px solid #d7d7d7;
  justify-content: space-between;
  padding: 16px 28px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.chat__leftbar-mesitem:hover {
  background: #f6f6f6;
}

.chat__leftbar-mesitem-left {
  align-items: center;
  display: flex;
}

.chat__leftbar-mesitem-avatar {
  width: 40px;
  height: 40px;
  background: #d9d9d9;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  margin-right: 12px;
  display: flex;
}

.chat__leftbar-mesitem-avatar i {
  width: 23px;
  height: 23px;
  background: url("user.5bbfb0d2.svg") center / cover no-repeat;
  display: block;
}

.chat__leftbar-mesitem-name {
  color: #000;
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
}

.chat__leftbar-mesitem-msg, .chat__leftbar-mesitem-time {
  color: #7a7a7a;
  font-size: 10px;
  font-weight: 400;
  line-height: 12px;
}

.chat__dialog {
  background: #6ca7ff24;
}

.chat__dialog--empty {
  width: calc(100% - 396px);
